import React from 'react'
import PropType from 'prop-types'
import cx from 'classnames'

import './style.scss'

export default class Decorate1 extends React.PureComponent {
  static defaultProps = {
    count: 12,
    itemWidth: 3,
    duration: 1000
  }

  render () {
    const props = this.props
    return (
      <div style={props.style} className={cx('dui-decorate4', props.className)}>
        {
          Array.from({ length: props.count }).map((v, i) => {
            const subStyle = {
              width: props.itemWidth,
              height: Math.round(0.5 * 100 * Math.random() + 0.5 * 100) + '%',
              animationDuration: `${props.duration}ms`,
              animationDelay: `${Math.random() * 2000}ms`
            }
            return <span key={i} style={subStyle} className={cx('dui-decorate4-item', props.barClassName)} />
          })
        }
      </div>
    )
  }
}

Decorate1.propType = {
  count: PropType.number,
  itemWidth: PropType.number,
  duration: PropType.number
}
